// Serach Bar

.search-bar{
	@include u-margin-top(1);
	@include u-margin-bottom(4);
	@include at-media('tablet') {
		@include u-margin-bottom(5);
	}
	h3{
		@include u-margin(0);
		@include u-margin-bottom(2);
		@include u-font('sans', '2xl');
		@include u-text('thin');
		@include u-text('center');
		@include u-text('indigo-warm-80');
	}
	.search-promo{
		@include u-margin-y('05');
		@include u-margin-x('05');
		@include u-font('sans', '3xs');
		strong{
			@include u-text('indigo-warm-60');
		}
		@include u-text('orange-40');
	}
}

.search_form{
	@include u-width('full');
	@include u-margin-top(2);
	@include at-media('tablet') {
		@include u-margin-top(1);
	}

	.search_field{
		@include u-width('full');
		@include u-height(6);
		@include u-font('sans', 'lg');
		@include u-text('light');
		@include u-text('indigo-warm-80');
		@include u-padding-left('105');
		@include u-padding-bottom('05');
		@include u-radius-left('md');
		@include u-border('2px', 'solid', 'indigo-warm-70');
		@include u-border-right(0);

		&::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
		  @include u-text('gray-cool-70');
			@include u-text('thin');
		  opacity: 1; /* Firefox */
		}

		&:-ms-input-placeholder { /* Internet Explorer 10-11 */
		  @include u-text('gray-cool-70');
			@include u-text('thin');
		}

		&::-ms-input-placeholder { /* Microsoft Edge */
		  @include u-text('gray-cool-70');
			@include u-text('thin');
		}
	}

	button{
		@include u-margin-top(0);
		padding: 0 12px;
		vertical-align: top;
		@include u-font('sans', 'md');
		@include u-height(6);

		@include u-radius-right('md');
		@include u-border('2px', 'solid', 'indigo-warm-60v');
		@include u-border-left(0);
		@include u-bg('white');
		&:hover{
			@include u-bg('indigo-warm-40v');
		}

		// tablet-lg and above
		@include at-media('tablet-lg') {
			@include u-position('absolute');
			right:6px;
		}
		img{
			width:1.4em;
			position: relative;
			top:4px;
		}
	}
}
